import React, { useState } from 'react';
import {Button, TextField, Fab, InputAdornment, makeStyles} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';

import { useDispatch, useSelector } from "react-redux";
import { changeReponse, peutSupprimer, deleteReponse, setUnite} from '../../slice/RepondreQuestionsSlice'
// import { selectUnites } from '../../slice/UniteSlice'

import ChoixUnite from '../unite/ChoixUnite';
import { afficherUnites } from '../unite/UniteFunctions'

export default function Item(props) {

     const useStyles = makeStyles((theme) => ({
          buttonDelete: {
               color: "white",
               backgroundColor: theme.palette.error.main,
               "&:hover": {
                    backgroundColor: theme.palette.error.dark
               },
          },
          reponse: {
               margin : "5px 0px",
               display : "flex",
               alignItems : "center",
               columnGap: "10px"
          },
          inputAdornmentReponse: {
               width : "100px",
          }
     }));
     const classes = useStyles();

     //const unitesReference = useSelector(selectUnites)

     //indique si la boite de dialogue choix unit est ouverte ou ferme
     const [choixUniteOpen, setOpen] = useState(false);

     const dispatch = useDispatch();

     //boolen qui autorise la suppression d'une rponse si il y a plus d'une rponse affich
     const canDelete = useSelector(peutSupprimer(props.indexQuestion))

     //s'occupe de changer la rponse au fur et  mesure que l'tudiant la tape
     //s'assure que la rponse soit un nombre
     const handleChangeReponse = (value) =>{
          dispatch(changeReponse({
               indexQuestion : props.indexQuestion, 
               indexReponse : props.num, 
               value : value
          }))
     }

     //ds que la rponse perd le focus, s'assure que la rponse ne puisse tre un '-' tout seul
     const handleBlurReponse = () =>{
          if (props.reponse.value === '-'){
               dispatch(changeReponse({
                    indexQuestion : props.indexQuestion, 
                    indexReponse : props.num, 
                    value : ''
               }))
          }
     }

     //gere la suppression de cette rponse
     const handleDeleteReponse = () =>{
          dispatch(deleteReponse({
               indexQuestion : props.indexQuestion,
               indexReponse : props.num,
          }))
     }

     //s'occupe de fermer la boite de dialogue du choix de l'unit
     //et de s'occuper des diffrentes actions demande par la boite  sa fermeture
     //paramtre : string (seul "annuler" ou "appliquer a tous" sont grs)
     const handleClose = () =>{
          //ferme la boite de dialogue
          setOpen(false);
     }

     //s'occupe de l'ouverture de la boite de dialogue choixUnit
     const handleOpen = () =>{
          //ouvre la boite de dialogue
          setOpen(true);
     }

     //remplace le tableau d'units de cette rponse
     const setUniteReponse = (tab) =>{
          dispatch(setUnite({
               indexQuestion : props.indexQuestion, 
               indexReponse : props.num,
               newTab : tab
          }))
     }

     /*//affiche l'unit en entire de la rponse (en mettant les puissances)
     const afficherUnite = () =>{
          return(
               props.reponse.tabUnite.map((i, index) => (
                    <var key={index}>
                         {i.abr} 
                         <sup> {i.puissance !== 1 && i.abr !== " " ? i.puissance : null } </sup>
                    </var>
                    )
               )
          )
     }*/

     //affiche le bouton pour supprimer cette rponse
     const buttonDelete = () =>{ return(
               <Fab className={classes.buttonDelete} size="small" aria-label="delete"
                    onClick={handleDeleteReponse}
               >
                    <DeleteIcon />
               </Fab>
     )}

     return(
          <div className={classes.reponse}>
               {/* input de la rponse */}
               <TextField label={"Reponse " + (props.num + 1)} variant="outlined" size="small" 
               value={props.reponse.value} onChange={e => handleChangeReponse(e.target.value)}
               onBlur={handleBlurReponse}
               InputProps={{ //affichage unit
                    endAdornment: (
                         <InputAdornment className={classes.inputAdornmentReponse} position="start">
                         {afficherUnites(props.reponse.tabUnite)}                               
                         </InputAdornment>
                    ),
               }} />

               {/* bouton pour changer l'unit (ouvre la boite de dialogue) */}
               <Button size="small" onClick={e=>handleOpen()}>Unite</Button>

               {/* boite de dialogue pour changer l'unit */}
               <ChoixUnite open={choixUniteOpen} unite={props.reponse.tabUnite} 
                    setTabUnite={setUniteReponse} handleClose={handleClose} />

               {/* affiche le bouton de suppression si il est possible de supprimer */}
               {canDelete ? buttonDelete() : null}
          </div>
     )

}
