import React, {useState} from 'react';
import _ from "lodash"
import {Button, IconButton, Dialog, DialogActions, DialogContent, DialogTitle, makeStyles} from '@material-ui/core';
import {TextField, MenuItem, InputAdornment} from '@material-ui/core';
import LoopIcon from '@material-ui/icons/Loop';
import DeleteIcon from '@material-ui/icons/Delete';


import { useDispatch, useSelector } from 'react-redux';
import { selectUnites, getAllUnite } from '../../slice/UniteSlice'
import useConstructor from '../use/useContructor';

//props
//  => open (boolen pour savoir si le dialog est ouvert ou ferm)
//  => unite (tableau de parties units faisant l'unit d'une rponse [{abr : string, puissance : int }])
//  => setTabUnite() (fonction changeant le tableau d'units)
//  => handleClose() (fonction appel  la fermeture du dialog (doit s'occupe de fermer le dialog))
export default function ChoixUnite(props){

    const useStyles = makeStyles((theme) => ({
        alignementHorizontal: {
            display : "flex",
            flexDirection: "row",
            justifyContent: "center",
            columnGap: "20px",
            alignItems: "center",
            flexWrap: "wrap"
        },
        buttonDelete: {
            color: theme.palette.error.main,
            "&:hover": {
                color: theme.palette.error.dark,
            }
        },
        buttonGap: {
            display: "flex",
            justifyContent: "center",
            columnGap: "10px"
        },
        choixInput: {
            marginBottom : "10px"
        },
        puissance: {
            width: "40px"
        }
    }));
    const classes = useStyles();

    //Partie unit : 'Kg^3' est une partie d'unit de 'm^2.Kg^3'
    //caractris par un    id (identifiant de l'unit dans le tableau unites)
    //                      puissance

    const [actualise, setActualise] = useState(false)

    const [tabUnites, setUnites] = useState([{abr : "", puissance : 1}])

    const dispatch = useDispatch();  

    const unitesReference = useSelector(selectUnites)

    useConstructor(() => {
        if (unitesReference.length === 0){ 
            dispatch(getAllUnite())
        }
        //dispatch(setTest())
    });

    const NB_MAX_PARTIES_UNITE = 6
    const MAX_WIDTH = "lg" //taille de la boite de dialogue

    //gre l'ajout d'une partie d'unit dans l'unit de la rponse
    const handleAjouterUnite = () =>{
        let newTab =[...tabUnites, {abr : "", puissance : 1}]
        setUnites(newTab)
    }

    //remete l'unit  [{id : 0, puissance : 1}], soit sans unit
    const handleRemettreAZero = () => {
        setUnites([{abr : "", puissance : 1}])
    }

    const actualiseOpen = () =>{
        if(props.open && !actualise){
            setUnites(_.cloneDeep(props.unite))
            setActualise(true)
        }else if(!props.open && actualise){
            setActualise(false)
        }
    }

    //gre le changement d'unit par action sur le select
    const handleChangeUnite = (index, event) =>{
        let newTab = [...tabUnites]
        newTab[index].abr = event.target.value
        setUnites(newTab)
    }

    //gre la suppression de cette partie d'unit
    const handleDeleteUnite = (index) =>{
        let newTab = [...tabUnites]
        newTab.splice(index, 1)
        setUnites(newTab)
    }

    //gre le changement de la puissance au fur et  mesure que l'tudiant la tape
    //n'accepte que nombres entre -100 et 100
    const handleChangePuissance = (index, event) =>{
        let value = event.target.value
        if((!isNaN(value)&& Math.abs(value) < 100 ) || value==='-' ){
            changePuissance(index, value)
        }
    }

    //lorque l'on quitte le focus sur la puissance, s'assure que la puissance soit bonne
    //si valeur est gal  0, ou  '-' ou  '', transforme en 1
    const handleBlurUnite = (index) =>{
        let pow = tabUnites[index].puissance
        if(parseInt(pow) === 0 || pow === '-' || pow === ''){
            changePuissance(index, 1)
        }
    }

    //change directement la puissance d'une unit
    const changePuissance = (index, value) =>{
        let newTab = [...tabUnites]
        newTab[index].puissance = value
        setUnites(newTab)
    }

    //gre la fermeture de la fentre avec annulation
    const handleAnnuler = () =>{
        props.handleClose()
    }

    //gre la fermeture de la fentre avec application
    const handleAppliquer = () =>{
        supprimerIterationsSansUnite()
        props.setTabUnite(tabUnites)
        props.handleClose()
    }

    //supprime les itrations de Sans Unit (abr : " ") qui ne sont pas utiles
    //si le tableau ne contient que des Sans unit, il n'en restera qu'un
    const supprimerIterationsSansUnite = () =>{
        _.remove(tabUnites, function(o){
            return o.abr === ""
        })

        if (tabUnites.length === 0){
            tabUnites.push({abr : "", puissance : 1})
        }

    }

    //affiche une partie d'unit
    const partieUnite = (partieUnite, index) => {
        return(
            <div className={classes.choixInput}>
              <div className={classes.buttonGap}>
                {tabUnites.length > 1 
                ? 
                <IconButton className={classes.buttonDelete} size="small" onClick={e=>handleDeleteUnite(index)} >
                    <DeleteIcon/>
                </IconButton>
                : null 
                }
              </div>
      
              {/* Select de l'unit */}
              {console.log(partieUnite)}
              {console.log(unitesReference)}
              <TextField select value={partieUnite.abr} onChange={e=>handleChangeUnite(index, e)}>
                  {unitesReference.map((i) => 
                  <MenuItem key={i.abrev} value={i.abrev} >
                      {i.nom}
                  </MenuItem>)}
              </TextField>
      
              {/* affiche la modif de puissance que si n'est pas Sans unit */}
              {partieUnite.abr !== "" ? 
              <>
              {/* modif puissance */}
              <TextField value={partieUnite.puissance} className={classes.puissance}
                  onChange={e=>handleChangePuissance(index, e)}
                  onBlur={e=>handleBlurUnite(index)}
                  InputProps={{ startAdornment: (
                          <InputAdornment position="start">
                            ^
                          </InputAdornment>
                          ),
                      }} />
              </>
              : null }
            </div>
          )
    }



    return(
        <Dialog 
            open={props.open} //ouverture gre dans ItemReponse
            maxWidth={MAX_WIDTH}
            fullWidth={false}
        >
            {actualiseOpen()}
            <DialogTitle className={classes.alignementHorizontal}>
                Choix de l'unit
            </DialogTitle>

            <DialogContent>
                <div className={classes.alignementHorizontal}>
                    {/* affiche un  un les diffrentes parties d'units*/}
                    {tabUnites.map((i, index) => 
                        <div key={index}>
                        {partieUnite(i, index)}
                        
                        {/* interserction avec des . entre les parties d'unit */}
                        {/*index < tabUnites.length-1 ? <b>.</b> : null*/}
                        </div>
                    )}
                </div>

                {/* boutons d'action dans la fentre */}
                <div className={classes.alignementHorizontal}>

                    {/* bouton ajout de partie d"unit */}
                    <Button 
                        variant="contained"
                        color="primary"
                        onClick={handleAjouterUnite}
                        
                        //disabled si on atteint la limite de partie d'unit (6)
                        disabled={tabUnites.length >= NB_MAX_PARTIES_UNITE }
                    >
                        Ajouter Unite
                    </Button>

                    {/* bouton pour remettre  zro cette unit en entire */}
                    <IconButton size="small" color="primary" onClick={handleRemettreAZero} 
                        title="remettre  0">
                        <LoopIcon/>
                    </IconButton>

                </div>
            </DialogContent>

            {/* boutons de fermeture de boite de dialogue */}
            <DialogActions>

                {/* bouton pour annuler la modification de l'unit */}
                <Button onClick={handleAnnuler}>
                    Annuler
                </Button>

                {/* bouton pour appliquer  toutes les rponses de la question cette mme unit */}
                {/*
                <Button onClick={e=>props.handleClose("appliquer a tous")} 
                    title="appliquer l'unit  toutes les rponses de la question">
                    Appliquer  tous
                </Button>
                */}

                {/* bouton pour appliquer uniquement  cette rponse cette unit */}
                <Button onClick={handleAppliquer}>
                    Appliquer
                </Button>
                
            </DialogActions>
        </Dialog>
    )

}